import React from "react";
import CardItem from "./CardItem";
import "./Cards.css";

function Cards() {
	return (
		<div className="cards">
			<div className="container">
				<div className="cards__wrapper">
					<ul className="cards__items">
						<CardItem src="images/mainpage.jpg" path="/culture" />
					</ul>
				</div>
			</div>
		</div>
	);
}

export default Cards;



